@import "./_base";
@import "./_variable";

@import url('https://fonts.googleapis.com/css2?family=Lato:wght@400;700&family=Noto+Sans+TC:wght@400;700&family=Roboto:ital,wght@1,500&display=swap');

@include keyframes(swing) {
  0% {
    transform: rotate(5deg); }
  100% {
    transform: rotate(-5deg); } }

* {
  padding: 0;
  margin: 0;
  font-family: 'Noto Sans TC', sans-serif;
  color: $gray01; }

/* Chrome, Safari, Edge, Opera */
input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0; }

/* Firefox */
input[type="number"] {
  -moz-appearance: textfield; }

img {
  width: 100%;
  vertical-align: middle; }

header {
  width: 100%;
  padding: 15px 30px;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  align-items: center;
  figure.logo {
    width: 145px; }
  .box {
    p.txt {
      padding: 0 10px;
      font-size: 1rem;
      color: $gray05;
      @include dib;
      & + p.txt {
        margin-right: 80px; } }
    figure.share {
      width: 80px;
      margin: 0 10px;
      cursor: pointer;
      @include dib; } }
  @include rwd(1000px) {
    // padding: 15px 30px
    // display: flex
    figure.logo {
 }      // width: 145px
    .box {
      p.txt {
        padding: 0 5px;
        font-size: 0.8rem;
        & + p.txt {
          margin-right: 10px; } }
      figure.share {
        width: 50px;
        margin: 0 5px; } } }
  @include rwd(768px) {
    padding: 10px 15px;
    .box {
      p.txt {
        display: none; } } } }

figure.fix-btn {
  width: 200px;
  position: fixed;
  right: 2rem;
  bottom: 6rem;
  z-index: 100;
  @include rwd(1280px) {
    width: 150px;
    right: 1rem;
    bottom: 3rem; }
  @include rwd(768px) {
    width: 80px;
    right: 0.5rem;
    bottom: 1rem; }
  @include rwd(540px) {
    width: 60px;
    right: 0.2rem;
    bottom: 0.5rem; } }


html {
  font-size: 20px;
  @include rwd(768px) {
    font-size: 16px; }
  article {
    section.banner {
      background-color: $red04;
      .main {
        width: calc(100% - 30px);
        max-width: 1200px;
        padding-top: 30px;
        margin: auto;
        position: relative;
        figure.title {
          width: 87.5%;
          margin: auto; }
        figure.kv {
          width: 80%;
          padding-right: 80px;
          margin: -9% auto -4%; }
        .dialogue {
          padding: 0.5rem 1rem;
          font-size: 1.3rem;
          color: $green04;
          background-color: #fff;
          text-align: center;
          position: absolute;
          &.dialogue1 {
            border-radius: 1rem 1rem 0 1rem;
            top: 40%;
            left: 0;
            @include before {
              width: 0;
              height: 0;
              border-top: 12px solid #fff;
              border-right: 15px solid #fff;
              border-bottom: 12px solid transparent;
              border-left: 15px solid transparent;
              position: absolute;
              right: 0;
              bottom: -24px; } }
          &.dialogue2 {
            border-radius: 1rem 1rem 1rem 0;
            top: 50%;
            right: 2.8%;
            @include before {
              width: 0;
              height: 0;
              border-top: 12px solid #fff;
              border-right: 15px solid transparent;
              border-bottom: 12px solid transparent;
              border-left: 15px solid #fff;
              position: absolute;
              left: 0;
              bottom: -24px; } } } }
      .slogan {
        padding: 5px 0;
        text-align: center;
        background-color: $red01;
        figure.icon {
          width: 50px;
          @include dib; }
        p {
          padding: 0 1rem;
          font-size: 1.7rem;
          color: #fff;
          @include dib; }
        figure.btn {
          width: 223px;
          cursor: pointer;
          @include dib; } }
      @include rwd(1280px) {
        .main {
          .dialogue {
            padding: 0.5rem;
            font-size: 1rem; } }
        .slogan {
          figure.icon {
            width: 40px; }
          p {
            font-size: 1.4rem; }
          figure.btn {
            width: 200px; } } }
      @include rwd(768px) {
