            width: 90%;
            padding-right: 40px; }
          //   margin: -9% auto -4%
          .dialogue {
            padding: 0.3rem;
            font-size: 0.75rem;
            &.dialogue1 {
              border-radius: 0.5rem 0.5rem 0 0.5rem;
              top: 40%;
              left: 0;
              &:before {
                border-top: 6px solid #fff;
                border-right: 7px solid #fff;
                border-bottom: 6px solid transparent;
                border-left: 7px solid transparent;
                bottom: -12px; } }
            &.dialogue2 {
              border-radius: 0.5rem 0.5rem 0.5rem 0;
